<template>
    <div class="row">
        <div class="col-xs-12 col-sm-8 col-sm-offset-2 col-md-6 col-md-offset-3">
            <h1 style="text-align: center; margin:30px;"> </h1>
            <div  v-if="loading" style="width: 100%; text-align: center;">
                <img style="margin: auto;" src="/static/loading.gif" />
            </div>
            <div v-else>
                <h4 style="text-align: center; margin:30px;">        </h4>
                <div class="form-group">
                    <label> </label>
                    <input class="form-control" type="text" v-model="group.name">
                </div>
                <button class="btn btn-primary" @click="submit"></button>
            </div>
        </div>
    </div>
</template>

<script>
    import axios from 'axios';

    export default {
        data() {
            return {
                group: {
                    name: ''
                },
                loading: false
            };
        },
        methods: {
            submit() {
                this.loading = true;
                var vinst = this;
                var host =  window.location.href.split("/")[0] + "//" +  window.location.href.split("/")[2]
                axios.post(host + '/api/team/create/', 
                this.group, // the data to post
                { headers: {
                'Content-type': 'application/json',
                'Authorization' : 'JWT ' + localStorage.getItem('t')
                }
                }).then(response => {
                            console.log("good");
                            console.log(response.data);
                            if(response.statusText == "Created"){
                                alert("     ");
                            }
                            vinst.loading = false;
                            this.$router.push({ name: 'submit-product' });
                        })
                        .catch(err => {
                            console.log("bad");
                            console.log(err.response);
                            if(err.response.data.name){
                                alert("Group name: " + err.response.data.name[0]);
                            }
                            vinst.loading = false;
                        });
            }
        }
    }
</script>

<style scoped>
h1, h4, label, button{
    font-family: tahoma;
}
</style>


